  margin-right: 10px;
}

.logo-first-word {
  color: var(--main-color);
  margin-right: 5px;
}

.header-menu {
  display: flex;
}

.header-menu li {
  margin-right: 40px;
}

.header-menu li:last-child {
  margin-right: 0;
}

.header-menu a {
  color: var(--secondary-text-color);
  padding: 20px 0;
  line-height: 1.56;
  display: block;
}

.header-menu a:hover, .header-menu a:focus {
  text-decoration: underline;
