.business-cases-title {
  text-align: center;
  color: var(--main-text-color);
  margin-bottom: 34px;
  font-size: 38px;
  font-style: normal;
  font-weight: 300;
  line-height: 1.26;
}

.business-cases-text {
  text-align: center;
  color: var(--main-text-color);
  margin-bottom: 39px;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.56;
}

.all-our-cases {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 8px;
  display: flex;
}

.business-cases-list {
  width: calc(25% - 6px);
}

.business-cases-image {
  justify-content: space-between;
