.net-link {
  width: 100%;
  height: 100%;
  color: #fff;
}

.net-icon {
  fill: currentColor;
}

.copyright {
  font-family: var(--main-font);
  text-align: center;
  color: #fff;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.56;
}

.section-callback {
  width: 1360px;
